#VenueDescription {
  flex: 0 1 30%;  
  text-align: center;
  width: calc((100% /3 ) - 1em);
  margin: 0 auto;
  padding: 3%;
  font-size: 20px;
  background-color: purple;
  opacity: 0.6  ;
  box-shadow: grey 5px 5px 5px;
}

#header {
    top : 0;
    width:100%;
    height: auto;
}
    
#header .navButtonsStyle{
    color: #800080; 
}

.list-item a{
    margin : 0 1em;
}
#footer {
    position: fixed;
    bottom: 0 ;
    width: 100%;
    clear: both;
    height: 40px;
    margin-top: auto;
}
#footer a{
    margin : 0 0.2em;
}

.list-item a{
        margin : 0 1em;
        text-decoration: none;
    }
#footer .navButtonsStyle{
    color: #800080; 

}
#footer-right{
    float: right;
}
#footer-right a{
    text-decoration: none;
}
.footer-left{
    float: left;
}
.footer-left a{
    text-decoration: none;
}


.favourites .frame{
    display : flex;
       flex-wrap: wrap;
       justify-content: space-between;
       

}
.frame div{
    flex: 0 1 30%;  
    text-align: center;
    width: calc((100% /3 ) - 10em);
    margin: 2em 0;
  
}
.notFound div {
    padding-left: 600px;
    color:white;
    font-size: 40px;
}
